@keyframes visible1 {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(150px) translateX(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px) translateX(0px);
  }
}
@keyframes comingFromRightAn {
  from {
    opacity: 0;
    /* left: 0; */

    transform: translateX(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}
@keyframes comingFromLeftAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateX(-150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateX(0px);
  }
}

@keyframes comingFromUpAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(-150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px);
  }
}

@keyframes comingFromDownAn {
  from {
    opacity: 0;
    /* left: 0; */
    transform: translateY(150px);
  }
  to {
    opacity: 100;
    /* left: 1000; */
    transform: translateY(0px);
  }
}

@keyframes growOutAn {
  from {
    opacity: 0;
    width: 0%;
    height: 0%;
  }
  to {
    opacity: 100;
    width: 100%;
    height: 100%;
  }
}

/* .notification {
    animation: visible1 1s;
  } */
.growOutP5 {
  animation: growOutAn 0.5s;
}
.growOut {
  animation: growOutAn 1s;
}
.comingFromLeft {
  animation: comingFromLeftAn 1s;
}
.comingFromRight {
  animation: comingFromRightAn 1s;
}
.comingFromDown {
  animation: comingFromDownAn 1s;
}
.comingFromUp {
  animation: comingFromUpAn 1s;
}

.comingFromLeftP5 {
  animation: comingFromLeftAn 0.5s;
}
.comingFromRightP5 {
  animation: comingFromRightAn 0.5s;
}
.comingFromDownP5 {
  animation: comingFromDownAn 0.5s;
}
.comingFromUpP5 {
  animation: comingFromUpAn 0.5s;
}
